import { useCallback, useEffect, useRef, useState } from "react"

export const useHttpClient = () => {
    const [loading, setLoading] = useState(false);
    const [errorState, setErrorState] = useState(null);
    const activeHttpRequest = useRef([]);
    const sendRequest = useCallback(async (url, method = 'GET', body = null, headers = {}) => {
        setLoading(true);
        
        try {
            const httpAbortCtrl = new AbortController();
            activeHttpRequest.current.push(httpAbortCtrl);
            
            const response = await fetch(url, { method, body, headers, signal: httpAbortCtrl.signal })
            const data = await response.json();
            activeHttpRequest.current = activeHttpRequest.current.filter(reqCtrl => reqCtrl !== httpAbortCtrl)
            if (!response.ok) {

                throw new Error(data.message)
            }
            setLoading(false);
            return data;
        } catch (error) {
            setLoading(false);
            setErrorState(error.message || 'Something went wrong, please try again')
            throw error

        }
    }, [])
    const cancelError = () => {
        setErrorState(null);
    }
    useEffect(() => {
        return () => {
            activeHttpRequest.current.forEach(abortCtrl => abortCtrl.abort())
        }
    }, [])
    return { loading, errorState, sendRequest, cancelError }
}