import React, { useEffect, useReducer } from 'react';
import { validate } from '../../util/validators';

import './Input.css';

// reducer function
// receives current state and the action that will be dispatched
const inputReducer = (state, action) => {
    // in this function we allways return a new state
    // action: Object{type, val}
    switch(action.type){
        case 'CHANGE':
            return {
                ...state,
                value:action.val,
                isTouched: action.isTouched,
                isValid : validate(action.val, action.validators) // will be replaced later
            };
        default:
            return state;
    }
}

function Input(props) {
   
    
    // takes a reducer function, and an initial state
    // returns an array of two elements [currentState,  dispatchFunction]
    // useReducer re-renders the component
    const [inputState, dispatch] = useReducer(inputReducer, {value : props.value || '', isValid: props.valid|| false, isTouched: false});
    const {id, onInput} = props;
    const {value, isValid} = inputState;
    useEffect(()=>{
        props.onInput(id, value, isValid);
    }, [id, onInput, value, isValid])

    const changeHandler = (event) => {
        dispatch({type : 'CHANGE', val: event.target.value, validators:props.validators, isTouched: true})
    }




    let element = null;
    switch(props.element){
        case 'input':
            element = <input value={inputState.value} onChange={changeHandler} id={props.id} type={props.type} placeholder={props.placeholder} />;
            break;
        case 'textarea':
            element = <textarea value={inputState.value} onChange={changeHandler} id={props.id} rows={props.rows || 3} />
            break;
        default:
            element = <input value={inputState.value} onChange={changeHandler} />
    }

   
    return (
        <div className={`form-control ${(!inputState.isValid && inputState.isTouched) && 'form-control--invalid'}`}>
            <label htmlFor={props.id}>{props.label}</label>
            {element}
            {(!inputState.isValid && inputState.isTouched) && <p>{props.errorText}</p>}
        </div>
    );
}

export default Input;